import React, { Component } from 'react'
import AuthApiService from '../services/auth-api-service'
import TokenService from '../services/token-service'
import IdleService from '../services/idle-service'

const UserContext = React.createContext({
  user: {},
  userDetails: {},
  currentWord: {},
  nextWord: {},
  totalScore: 0,
  wordCorrectCount: 0,
  wordIncorrectCount: 0,
  isCorrect: null,
  answer: null,
  guess: {},
  error: null,
  setError: () => {},
  clearError: () => {},
  setUser: () => {},
  setUserDetails: () => {},
  setCurrentWord: () => {},
  setGuess: () => {},
  updateNextWord: () => {},
  updateTotalScore: () => {},
  updateWordCorrectCount: () => {},
  updateWordIncorrectCount: () => {},
  updateIsCorrect: () => {},
  updateAnswer: () => {},
  processLogin: () => {},
  processLogout: () => {},
})

export default UserContext

export class UserProvider extends Component {
  constructor(props) {
    super(props)
    const state = { user: {}, userDetails: {}, currentWord: {}, nextWord: '', totalScore: 0, wordCorrectCount: 0, wordIncorrectCount: 0, isCorrect: null, answer: null, guess: {},  error: null }

    const jwtPayload = TokenService.parseAuthToken()

    if (jwtPayload)
      state.user = {
        id: jwtPayload.user_id,
        name: jwtPayload.name,
        username: jwtPayload.sub,
      }

    this.state = state;
    IdleService.setIdleCallback(this.logoutBecauseIdle)
  }

  componentDidMount() {
    if (TokenService.hasAuthToken()) {
      IdleService.regiserIdleTimerResets()
      TokenService.queueCallbackBeforeExpiry(() => {
        this.fetchRefreshToken()
      })
    }
  }

  componentWillUnmount() {
    IdleService.unRegisterIdleResets()
    TokenService.clearCallbackBeforeExpiry()
  }

  setError = error => {
    console.error(error)
    this.setState({ error })
  }

  clearError = () => {
    this.setState({ error: null })
  }

  setUser = user => {
    this.setState({ user })
  }

  setUserDetails = userDetails => {
    this.setState({ userDetails })
  }

  setCurrentWord = currentWord => {
    this.setState({ currentWord })
  }

  updateTotalScore = totalScore => {
    this.setState({ totalScore })
  }

  updateWordCorrectCount = wordCorrectCount => {
    this.setState({ wordCorrectCount })
  }

  updateWordIncorrectCount = wordIncorrectCount => {
    this.setState({ wordIncorrectCount })
  }

  updateIsCorrect = isCorrect => {
    this.setState({ isCorrect })
  }

  updateAnswer = answer => {
    this.setState({ answer })
  }

  updateNextWord = nextWord => {
    this.setState({ nextWord })
  }


  processLogin = authToken => {
    TokenService.saveAuthToken(authToken)
    const jwtPayload = TokenService.parseAuthToken()
    this.setUser({
      id: jwtPayload.user_id,
      name: jwtPayload.name,
      username: jwtPayload.sub,
    })
    IdleService.regiserIdleTimerResets()
    TokenService.queueCallbackBeforeExpiry(() => {
      this.fetchRefreshToken()
    })
  }

  processLogout = () => {
    TokenService.clearAuthToken()
    TokenService.clearCallbackBeforeExpiry()
    IdleService.unRegisterIdleResets()
    this.setUser({})
  }

  logoutBecauseIdle = () => {
    TokenService.clearAuthToken()
    TokenService.clearCallbackBeforeExpiry()
    IdleService.unRegisterIdleResets()
    this.setUser({ idle: true })
  }

  fetchRefreshToken = () => {
    AuthApiService.refreshToken()
      .then(res => {
        TokenService.saveAuthToken(res.authToken)
        TokenService.queueCallbackBeforeExpiry(() => {
          this.fetchRefreshToken()
        })
      })
      .catch(err => {
        this.setError(err)
      })
  }

  render() {
    const value = {
      user: this.state.user,
      userDetails: this.state.userDetails,
      currentWord: this.state.currentWord,
      nextWord: this.state.nextWord,
      totalScore: this.state.totalScore,
      wordCorrectCount: this.state.wordCorrectCount,
      wordIncorrectCount: this.state.wordIncorrectCount,
      answer: this.state.answer,
      isCorrect: this.state.isCorrect,
      guess: this.state.guess,
      error: this.state.error,
      setError: this.setError,
      clearError: this.clearError,
      setUser: this.setUser,
      setUserDetails: this.setUserDetails,
      setCurrentWord: this.setCurrentWord,
      updateNextWord: this.updateNextWord,
      updateTotalScore: this.updateTotalScore,
      updateWordCorrectCount: this.updateWordCorrectCount,
      updateWordIncorrectCount: this.updateWordIncorrectCount,
      updateAnswer: this.updateAnswer,
      updateIsCorrect: this.updateIsCorrect,
      processLogin: this.processLogin,
      processLogout: this.processLogout,
    }

    return (
      <UserContext.Provider value={value}>
        {this.props.children}
      </UserContext.Provider>
    )
  }
}
